












































import {Component, Vue} from 'vue-property-decorator';
import Pagination from '../../components/Pagination/index.vue';
import Query from './Query';
import {list} from '@/api/log';
import Data from '@/class/Data';

@Component({
    components: {
        Pagination,
    },
})
export default class User extends Vue {
    private query = new Query();
    private data = new Data();
    private dates: string[] = [];
    private pickerOptions = {
        shortcuts: [{
            text: '最近一周',
            onClick(picker: any) {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                picker.$emit('pick', [start, end]);
            },
        }, {
            text: '最近一个月',
            onClick(picker: any) {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                picker.$emit('pick', [start, end]);
            },
        }, {
            text: '最近三个月',
            onClick(picker: any) {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                picker.$emit('pick', [start, end]);
            },
        }],
    };

    private created() {
        this.getList();
    }

    private getList() {
        this.data.tableLoading = true;
        list(this.query).then((resp) => {
            const data = resp.data;
            this.data.list = data.rows;
            this.query.total = data.total;
            this.query.pageSize = data.pageSize;

            this.data.tableLoading = false;
        });
    }

    private dateChange(value: string[]) {
        if (value && value.length > 0) {
            this.query.startDate = value[0];
            this.query.endDate = value[1];
        } else {
            this.query.startDate = '';
            this.query.endDate = '';
        }
    }
}
